import React, { Component } from "react";

interface IProps {
    className?: string,
    checked?: boolean,
}

interface IState {
    checked: boolean,
}

class RadioComponent extends Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            checked: false,
        }

        this.onClick = this.onClick.bind(this);
    }

    componentWillMount() {
        if (typeof this.props.checked === 'boolean') this.setState({ checked: this.props.checked });
    }

    componentWillReceiveProps(nextProps: IProps) {
        if (typeof nextProps.checked !== 'undefined' && nextProps.checked !== this.props.checked) {
            this.setState({ checked: nextProps.checked })
        }
    }

    onClick() {
        this.setState((state)=>({...state, checked: !state.checked}));
    }

    render() {
        const { className } = this.props;
        const { checked } = this.state;

        return (
            <span className={`radio ${className ? className : ""} ${checked ? "checked" : ""}`}>
                <div className="radio-border" onClick={this.onClick}>
                    <div className="radio-check"></div>
                </div>
            </span>
        )
    }
}

export default RadioComponent;