import React, { Component } from "react";

interface IState {
    isShow: boolean
}
interface IProps { }

class BacktotopComponent extends Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            isShow: false,
        }
        this.onClick = this.onClick.bind(this);
    }

    componentDidMount() {
        window.addEventListener('scroll', () => {
            const { isShow } = this.state;
            if (window.pageYOffset >= window.innerHeight * 0.5 && !isShow) this.setState({ isShow: true })
            else if (window.pageYOffset < window.innerHeight * 0.5 && isShow) this.setState({ isShow: false })
        })
    }

    onClick() {
        var scrollToTop = window.setInterval(function () {
            var pos = window.pageYOffset;
            if (pos > 0) {
                window.scrollTo(0, pos - 20); // how far to scroll on each step
            } else {
                window.clearInterval(scrollToTop);
            }
        }, 10)
    }

    render() {
        const { isShow } = this.state;
        return (
            <div className={`backtotop ${isShow ? "backtotop-active" : ""}`} onClick={this.onClick}>
                <i className="fa fa-chevron-up"></i>
            </div>
        )
    }
}

export default BacktotopComponent;