import React, { Component } from "react";
import { Provider } from "react-redux";
import store from "./store";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import TopBarComponent from "./components/components.topbar";
import NavigatorComponent from "./components/components.navigator";
import FooterComponent from "./components/components.footer";
import BacktotopComponent from "./components/components.backtotop";
import IndexContent from "./content/content.index";
import GridProductContent from "./content/content.gridproduct";
import ProductDetailContent from "./content/content.productdetail";
import SearchContent from "./content/content.search";
import LoginContent from "./content/content.login";
import CartContent from "./content/content.cart";
import OrderContent from "./content/content.order";
import ProfileContent from "./content/content.profile";
import InfomationContent from "./content/content.infomation";

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <TopBarComponent />
          <NavigatorComponent />
          <Switch>
            <Route path="/" exact>
              <IndexContent />
            </Route>
            <Route path="/category">
              <GridProductContent />
            </Route>
            <Route path="/product">
              <ProductDetailContent />
            </Route>
            <Route path="/search">
              <SearchContent />
            </Route>
            <Route path="/login">
              <LoginContent />
            </Route>
            <Route path="/cart">
              <CartContent />
            </Route>
            <Route path="/order">
              <OrderContent />
            </Route>
            <Route path="/profile">
              <ProfileContent />
            </Route>
            <Route path="/infomation">
              <InfomationContent />
            </Route>
          </Switch>
          <FooterComponent />
          <BacktotopComponent />
        </Router>
      </Provider>
    );
  }
}

export default App;
