import React, { Component } from "react";
import Markdown from "markdown-it";
import { Tabs, Form, Tab, Button } from "react-bootstrap";

interface IProps { }
interface IState {
    tabKey: string
}

class DescriptionComponent extends Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            tabKey: "description"
        }

        this.onSelectTab = this.onSelectTab.bind(this);
    }

    onSelectTab(key: string) {
        this.setState({ tabKey: key })
    }

    render() {
        const { tabKey } = this.state;
        const md = new Markdown();

        return (
            <div className="description-component">
                <Tabs id="controlled-tab-example"
                    className="flex-row mr-0"
                    activeKey={tabKey}
                    onSelect={(key: any) => this.onSelectTab(key)}
                >
                    <Tab tabClassName="w-50 text-center m-0" eventKey="description" title="Chi tiết sản phẩm">
                        <div className="mt-3" dangerouslySetInnerHTML={{
                            __html: md.render(`
-   Xtyle ra mắt 3 dòng chống nắng thế hệ mới và tiên tiến nhất 2019: SUNSTOP AIR - UV CUT - UV MASTER
-   Bảo vệ tốt nhất - ngăn tới 99,4% tia UV được Viện Dệt May Việt Nam xét nghiệm xác nhận
-   Giúp giữ trắng da. Có chỉ số chống nắng UPF 50+ chặn đứng tác nhân gây đen sạm, lão hóa, ung thư da.
-   Tạo cảm giác dễ chịu nhất dưới nắng nóng gay gắt: mát mẻ, khô thoáng, co giãn tốt 
-   Khả năng làm mát đến từ công thức vải thoáng khí ưu việt: thoát khí nóng cùng mô hôi cơ thể cực nhanh - Hết nóng bức, dính bết mồ hôi.
-   Thiết kế thời trang, gọn gàng an toàn khi di chuyển 
-   Bảo vệ toàn diện với Áo khoác, Chân váy, Găng tay, khẩu trang :white_check_mark:Đầy đủ cho cả gia đình (Nam, nữ, trẻ em) 
-   Cam kết 1 đổi 1 trong 14 ngày nếu Quý Khách không hài lòng
                        `)
                        }}>
                        </div>
                    </Tab>
                    <Tab tabClassName="w-50 text-center m-0" eventKey="rate" title="Đánh giá">
                        <div className="mt-2">
                            <div className="border-bottom">
                                <p className="my-1">Bạn đang đánh giá:</p>
                                <p className="my-2 text-bold">Gel rửa tay khô 100ml</p>
                            </div>
                            <Form>
                                <Form.Group className="my-2">
                                    <Form.Label>Đánh giá</Form.Label>
                                    <Form.Control as="textarea" rows="3" />
                                </Form.Group>
                            </Form>
                            <Button variant="primary" className="mt-1">Gửi đánh giá</Button>
                        </div>
                    </Tab>
                </Tabs>
            </div>
        );
    }
}

export default DescriptionComponent;