import { EReponsiveType } from "../define/define.type";
import { RESPONSIVE } from "../define/define.key";

let initialState = {
    size: EReponsiveType.LG,
}

export default function reducerView(state = initialState, payload: any) {
    switch (payload.type) {
        case RESPONSIVE:
            return {...state, size: payload.size}
        default:
            return state;
    }
}