import React, { Component } from "react";
import { Carousel, Button } from "react-bootstrap";
import Slider from "react-slick";

interface IProps { }

interface IState {
    index: number,
    direction: any,
}

class CarouselComponent extends Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            index: 0,
            direction: null,
        }

        this.handleSelect = this.handleSelect.bind(this);
    }

    handleSelect(selectedIndex: number, e: any) {
        this.setState({
            index: selectedIndex,
            direction: e.direction
        })
    };

    render() {
        let { direction, index } = this.state;

        return (
            <div className="overflow-hidden">
                <Slider
                    arrows
                    slidesToScroll={1}
                    slidesToShow={1}
                    fade
                    dots
                    pauseOnHover={false}
                    autoplay
                    autoplaySpeed={5000}
                >
                    <div>
                        <img
                            src="https://4menshop.com/images/thumbs/slides/banner-top-trang-chu-2-slide-20.png?"
                            width="100%"
                            alt="First slide"
                        />
                    </div>
                    <div>
                        <img
                            src="https://4menshop.com/images/thumbs/slides/banner-top-trang-chu-1-slide-19.png"
                            width="100%"
                            alt="Second slide"
                        />
                    </div>
                </Slider>
            </div>
        );
    }
}

export default CarouselComponent;