import React, { Component } from "react";
import { Button, Form, Pagination, Row, Col } from "react-bootstrap";

interface IProps {
    position: "top" | "bottom",
    className?: string,
}
interface IState { }

class ToolBarComponent extends Component<IProps, IState> {
    render() {
        let { className, position } = this.props;
        className = className || "";

        return (
            <Row className={`gridproduct-toolbar m-0 ${position === 'top' ? 'pb-4 border-bottom' : 'pt-4 border-top'} ${className}`}>
                <Col xs={12} md={6} className={`toolbar-item toolbar-sort p-0 ${position === "bottom" ? "d-none d-sm-flex" : ""}`}>
                    <div className="mr-4 d-flex">
                        <Button variant="outline-secondary"><i className="fa fa-th"></i></Button>
                        <Button variant="outline-secondary"><i className="fa fa-th-list"></i></Button>
                    </div>
                    <Form.Label className="d-block">Sắp xếp theo: </Form.Label>
                    <Form.Control as="select">
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4</option>
                        <option>5</option>
                    </Form.Control>
                    <Button variant="outline-secondary">
                        <i className="fa fa-long-arrow-down text-12"></i>
                    </Button>
                </Col>
                <Col xs={12} md={6} className={`toolbar-item justify-content-center justify-content-md-end toolbar-pagination p-0 ${position === "top" ? "d-none d-sm-flex" : ""}`}>
                    <Form.Label className="d-none d-md-block">Trang: </Form.Label>
                    <Pagination>
                        <Pagination.Prev />
                        <Pagination.Item>{10}</Pagination.Item>
                        <Pagination.Item>{11}</Pagination.Item>
                        <Pagination.Item active>{12}</Pagination.Item>
                        <Pagination.Item>{13}</Pagination.Item>
                        <Pagination.Item>{14}</Pagination.Item>
                        <Pagination.Next />
                    </Pagination>
                </Col>
            </Row>
        )
    }
}

export default ToolBarComponent;