import React, { Component, SyntheticEvent } from "react";

export interface IDropDownProps {
    x?: number,
    margin?: 'left' | 'right',
    content?: JSX.Element,
}
interface IProps extends IDropDownProps {
    onMouseEnter: Function,
    onMouseLeave: Function
}
interface IState {
}

class NavigatorDropdownComponent extends Component<IProps, IState> {
    render() {
        const { onMouseEnter, onMouseLeave, content, x, margin } = this.props;
        //Style
        let style: { [key in string]: string } = {}
        if (margin && typeof x === "number") {
            if (margin === "left") style["left"] = `${x}px`
            if (margin === "right") style["right"] = `${window.innerWidth - x}px`
        }
        style["display"] = content? "block":"none";

        return (
            <div
                onMouseEnter={() => { onMouseEnter() }}
                onMouseLeave={() => { onMouseLeave() }}
                className="navbar-dropdown"
                style={style}
            >
                {content}
            </div>
        )
    }
}

export default NavigatorDropdownComponent;