import React, { Component } from "react";
import ToolBarComponent from "./gridproduct/components.gridproduct.toolbar";
import ListProductCommonComponent from "./common/components.common.listproduct";
import { listProductData as data } from "../data/data";
import { Col, Row } from "react-bootstrap";
import ProductItemCommonComponent from "./common/components.common.productitem";

interface IProps { }
interface IState { }

class GridProductComponents extends Component<IProps, IState> {
    render() {
        return (
            <div className="gridproduct">
                <ToolBarComponent position="top" />
                <Row className="py-3">
                    {
                        data.products.map(({ image, oldprice, price, title, badge }, i) => (
                            <ProductItemCommonComponent key={i} image={image} title={title} oldprice={oldprice} price={price} badge={badge} />
                        ))
                    }
                </Row>
                <ToolBarComponent position="bottom" />
            </div>
        )
    }
}

export default GridProductComponents;