import React, { Component } from "react";
import { Image, Button, Col, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import { EOrientation } from "../../define/define.type";

export interface IProduct {
    image: string,
    title: string,
    price: number,
    oldprice: number,
    control?: boolean,
    orientation?: EOrientation,
    badge?: string,
    responsive?: boolean,
}
interface IProps extends IProduct { }
interface IState { }

class ProductItemCommonComponent extends Component<IProps, IState> {
    render() {
        let { image, title, price, oldprice, badge, orientation, control, responsive } = this.props;
        control = typeof control !== 'undefined' ? control : true;
        responsive = typeof responsive !== 'undefined' ? responsive : true;
        orientation = typeof orientation !== 'undefined' ? orientation : EOrientation.POTRAIT;

        let potrait = (
            <Link to="/product">
                <div className="product-item d-flex flex-column align-items-center my-3">
                    <div className="product-item-image w-100">
                        {badge ? <div className={`badge text-uppercase text-13 ${badge.toLocaleLowerCase() === 'hot' || badge.toLocaleLowerCase() === 'new' ? badge : ''}`}>{badge}</div> : undefined}
                        <Image src={image} width="100%" />
                        {
                            control ? (
                                <>
                                    <div className="product-item-mask"></div>
                                    <div className="product-item-tool d-flex w-100">
                                        <Button variant="dark" className="m-0 rounded-0 btn-preview" block><i className="fa fa-search text-light text-16"></i></Button>
                                        <Button variant="dark" className="m-0 rounded-0 btn-cart" block><i className="fa fa-shopping-cart text-light text-16"></i></Button>
                                    </div>
                                </>
                            ) : undefined
                        }
                    </div>
                    <div className="product-item-info d-flex flex-column align-items-center mb-1">
                        <div><h4 className="text-center"><a className="link">{title}</a></h4></div>
                        <span className="text-red text-16">{price} {oldprice ? (<em>{oldprice}</em>) : (undefined)}</span>
                    </div>
                </div>
            </Link>
        )

        let lanscape = (
            <Link to="/product">
                <div className="product-item landscape container-fluid my-2">
                    <Row>
                        <Col xs={control ? 12 : 4} md={control ? 3 : 2}>
                            <div className="product-item-image w-100">
                                {badge ? <div className={`badge text-uppercase text-13 ${badge.toLocaleLowerCase() === 'hot' || badge.toLocaleLowerCase() === 'new' ? badge : ''}`}>{badge}</div> : undefined}
                                <Image src={image} width="100%" />
                            </div>
                        </Col>
                        <Col xs={control ? 12 : 8} md={control ? 9 : 10}>
                            <div className="product-item-info">
                                <div><h4 className="mt-2"><a className="link text-18">{title}</a></h4></div>
                                <div className="text-red text-16">{price} {oldprice ? (<em>{oldprice}</em>) : (undefined)}</div>
                                <div className={`${control ? "" : "m-0"}`}>{title}</div>
                                {
                                    control ? (
                                        <div className="product-item-control">
                                            <button className="btn btn-danger">
                                                <i className="fa fa-shopping-cart text-light d-none d-md-inline"></i> Mua sản phẩm
                                                                </button>
                                            <button className="btn btn-danger">
                                                Thêm vào giỏ hàng
                                                                </button>
                                            <button className="btn btn-outline-dark d-none d-md-inline">
                                                <i className="fa fa-heart"></i>
                                            </button>
                                        </div>
                                    ) : undefined
                                }
                            </div>
                        </Col>
                    </Row>
                </div>
            </Link>
        )

        return (
            <>
                {
                    orientation === EOrientation.POTRAIT ? (
                        responsive ? (<Col xs={6} md={3}>{potrait}</Col>) : potrait
                    ) : (
                            responsive ? (<Col xs={12}>{lanscape}</Col>) : lanscape
                        )
                }
            </>
        );
    }
}

export default ProductItemCommonComponent;