import React, { Component } from "react";
import { Row, Col, Carousel } from "react-bootstrap";
import ProductItemCommonComponent, { IProduct } from "./components.common.productitem";
import { EListProductTitle, EListProductList, EOrientation } from "../../define/define.type";
import Slider from "react-slick";

interface IProps {
    title: string,
    description?: string,
    icon?: string,
    products: IProduct[],
    titleType?: EListProductTitle
    listType?: EListProductList
    carouselItems?: number
}
interface IState { }

class ListProductCommonComponent extends Component<IProps, IState> {
    render() {
        let { title, description, products, icon, titleType, listType, carouselItems } = this.props;
        icon = icon || "fa fa-star";
        titleType = titleType !== undefined && titleType in EListProductTitle ? titleType : EListProductTitle.FULL
        listType = listType !== undefined && listType in EListProductList ? listType : EListProductList.LIST
        carouselItems = carouselItems !== undefined ? carouselItems : 4

        return (
            <div className="list-product container">
                <Row className="p-3">
                    <Col xs={12} className="mb-2">
                        {
                            titleType === EListProductTitle.FULL ? (
                                <div className="list-product-title-full d-flex flex-column align-items-center">
                                    <h5 className="text-bold text-23">{title}</h5>
                                    <div className="list-product-mark d-flex justify-content-center position-relative">
                                        <div className="list-product-mark-line mark-line position-absolute" />
                                        <i className={`${icon} px-2 text-23`}></i>
                                    </div>
                                    <p className="text-14 text-center py-3 m-0">{description}</p>
                                </div>
                            ) : (
                                    <div className="list-product-title-tiny position-relative">
                                        <div className="list-product-title-line mark-line position-absolute w-100" />
                                        <h5 className="list-product-title text-bold text-18 px-3">{title}</h5>
                                    </div>
                                )
                        }
                    </Col>
                    {
                        listType === EListProductList.LIST ? (
                            <div className="container-fluid">
                                <Row>
                                    {
                                        products.map(({ image, oldprice, price, title, badge }, i) => (
                                            <ProductItemCommonComponent key={i} image={image} title={title} oldprice={oldprice} price={price} badge={badge} />
                                        ))
                                    }
                                </Row>
                            </div>
                        ) : (
                                <Col xs={12} className="list-product-slider">
                                    <Slider
                                        arrows
                                        slidesToScroll={1}
                                        slidesToShow={carouselItems}
                                        responsive={[
                                            {
                                                breakpoint: 576,
                                                settings: {
                                                    slidesToShow: 1
                                                }
                                            }
                                        ]}
                                    >
                                        {
                                            products.map(({ image, oldprice, price, title, badge }, i) => (
                                                <div className="list-product-slider-item" key={i}>
                                                    <ProductItemCommonComponent
                                                        orientation={EOrientation.POTRAIT}
                                                        image={image}
                                                        title={title}
                                                        oldprice={oldprice}
                                                        price={price}
                                                        badge={badge}
                                                        responsive={false}
                                                    />
                                                </div>
                                            ))
                                        }
                                    </Slider>
                                </Col>
                            )
                    }
                </Row>
            </div >
        );
    }
}

export default ListProductCommonComponent;