import React, { Component, SyntheticEvent } from "react";
import { Navbar, Nav, Button, Image, Form } from "react-bootstrap";
import { IMegaDirectional } from "../define/define.interface";
import { ENavbarNav } from "../define/define.type";
import NavigatorCartComponent from "../components/navigator/components.navigator.cart";
import NavigatorUserComponent from "../components/navigator/components.navigator.user";
import NavigatorCategoryComponent from "../components/navigator/components.navigator.category";
import NavigatorDropdownComponent,{ IDropDownProps} from "../components/navigator/components.navigator.dropdown";
import { Link } from "react-router-dom";

import { navData } from "../data/data";

interface IProps { }
interface IState {
    navbarType: ENavbarNav,
    expanded: boolean,
    dropdown?: IDropDownProps,
    timeOutDropdown?: NodeJS.Timeout,
}

class NavigatorComponent extends Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            navbarType: ENavbarNav.CATEGORY,
            expanded: false,
            dropdown: undefined,
            timeOutDropdown: undefined,
        }

        this.onClickSearch = this.onClickSearch.bind(this);
        this.onClickCart = this.onClickCart.bind(this);
        this.onClickUser = this.onClickUser.bind(this);
        this.onClickMenu = this.onClickMenu.bind(this);
        this.onClickSearchNormal = this.onClickSearchNormal.bind(this);
        this.onMouseEnter = this.onMouseEnter.bind(this);
        this.onMouseLeave = this.onMouseLeave.bind(this);
    }

    componentDidMount() {
        this.addListenner();
    }

    addListenner() {
        let nav = document.getElementById('navigator');
        let navfilter = document.getElementById('navigator-filter');
        let bar = document.getElementById('topbar');

        window.addEventListener('scroll', () => {
            if (!nav || !bar || !navfilter) return;
            let top = (window.pageYOffset || nav.scrollTop) - (nav.clientTop || 0);
            let barh = bar.clientHeight;

            if (top >= barh && nav.getAttribute('style') !== 'position: fixed') {
                nav.setAttribute('style', 'position: fixed');
                navfilter.setAttribute('style', 'display: block; height: 71px');
            } else if (top < barh && nav.getAttribute('style') !== 'position: relative') {
                nav.setAttribute('style', 'position: relative');
                navfilter.setAttribute('style', 'display: none; height: 71px');
            }
        })
    }

    onClickSearch(event: SyntheticEvent) {
        this.setState((state) => ({ navbarType: ENavbarNav.SEARCH, expanded: state.navbarType !== ENavbarNav.SEARCH && state.expanded ? state.expanded : !state.expanded }))
    }

    onClickCart(event: SyntheticEvent) {
        this.setState((state) => ({ navbarType: ENavbarNav.CART, expanded: state.navbarType !== ENavbarNav.CART && state.expanded ? state.expanded : !state.expanded }))
    }

    onClickUser(event: SyntheticEvent) {
        this.setState((state) => ({ navbarType: ENavbarNav.USER, expanded: state.navbarType !== ENavbarNav.USER && state.expanded ? state.expanded : !state.expanded }))
    }

    onClickMenu(event: SyntheticEvent) {
        this.setState((state) => ({ navbarType: ENavbarNav.CATEGORY, expanded: state.navbarType !== ENavbarNav.CATEGORY && state.expanded ? state.expanded : !state.expanded }))
    }

    onClickSearchNormal(event: SyntheticEvent) {
        this.setState((state) => ({ navbarType: state.navbarType === ENavbarNav.CATEGORY ? ENavbarNav.SEARCH : ENavbarNav.CATEGORY }))
    }

    onMouseEnter(dropdown?: IDropDownProps) {
        let { timeOutDropdown } = this.state;
        //Stop timeout
        if (timeOutDropdown) {
            clearTimeout(timeOutDropdown);
            timeOutDropdown = undefined;
        }
        //Set data
        if (dropdown) this.setState({ dropdown: dropdown, timeOutDropdown: undefined })
    }

    onMouseLeave() {
        this.setState({
            timeOutDropdown: setTimeout(() => {
                if (this) this.setState({ dropdown: undefined, timeOutDropdown: undefined })
            }, 100)
        })
    }

    render() {
        let { navbarType, expanded, dropdown } = this.state;
        dropdown = dropdown || {}

        return (
            <header>
                <div id="navigator-filter"></div>
                <div id="navigator" className="navigator">
                    <div className="container">
                        <Navbar expand="lg" expanded={expanded}>
                            <Navbar.Brand href="#home"><Link to="/"><Image src="/static/images/logo.png" height="40px" /></Link></Navbar.Brand>
                            <div className="navbar-btn d-lg-none">
                                <Button variant="light" className="shadow-none" onClick={this.onClickSearch}><i className="fa fa-search"></i></Button>
                                <Button variant="light" className="shadow-none" onClick={this.onClickCart}><i className="fa fa-shopping-cart"></i></Button>
                                <Button variant="light" className="shadow-none" onClick={this.onClickUser}><i className="fa fa-user"></i></Button>
                                <Button variant="light" className="shadow-none" onClick={this.onClickMenu}><i className="fa fa-bars"></i></Button>
                            </div>
                            <Navbar.Collapse id="basic-navbar-nav mt-1  ">
                                {
                                    (() => {
                                        switch (navbarType) {
                                            case ENavbarNav.SEARCH:
                                                return (
                                                    <Nav className="w-100">
                                                        <Form.Control type="text" placeholder="Search" />
                                                    </Nav>
                                                )
                                            case ENavbarNav.CART:
                                                return (
                                                    <Nav className="m-0">
                                                        <NavigatorCartComponent />
                                                    </Nav>
                                                )
                                            case ENavbarNav.USER:
                                                return (
                                                    <Nav className="d-flex w-100 justify-content-center">
                                                        <NavigatorUserComponent />
                                                    </Nav>
                                                )
                                            default:
                                                return (
                                                    <Nav className="ml-auto">
                                                        {
                                                            navData.map(({ title, dropdown }, i) => (
                                                                <Nav.Link
                                                                    className="text-uppercase link mr-xl-5 mr-md-3"
                                                                    onMouseEnter={(event: SyntheticEvent) => {
                                                                        if (dropdown) this.onMouseEnter({
                                                                            content: <NavigatorCategoryComponent data={dropdown} />,
                                                                            x: (event.target as HTMLElement).getBoundingClientRect().left - 100 * (navData.length - i - 1),
                                                                            margin: 'left'
                                                                        })
                                                                    }}
                                                                    onMouseLeave={this.onMouseLeave}
                                                                    key={i}
                                                                >
                                                                    <Link to="/category">{title.text}</Link>{title.badge ? (<span className="badge">{title.badge}</span>) : (undefined)}
                                                                </Nav.Link>
                                                            ))
                                                        }
                                                    </Nav>
                                                )
                                        }
                                    })()
                                }
                            </Navbar.Collapse>
                            <div className="navbar-btn d-none d-lg-block">
                                <Button
                                    variant="light"
                                    onMouseEnter={() => { }}
                                    onMouseLeave={() => { }}
                                    onClick={this.onClickSearchNormal}
                                >
                                    <i className="fa fa-search"></i>
                                </Button>
                                <Button
                                    variant="light"
                                    onMouseEnter={(event: SyntheticEvent) => {
                                        console.log((event.target as any).offsetLeft)
                                        this.onMouseEnter({
                                            content: <NavigatorCartComponent />,
                                            x: (event.target as HTMLElement).getBoundingClientRect().right,
                                            margin: 'right'
                                        })
                                    }}
                                    onMouseLeave={this.onMouseLeave}
                                >
                                    <Link to="/cart">
                                        <i className="fa fa-shopping-cart"></i>
                                    </Link>
                                </Button>
                                <Button
                                    variant="light"
                                    onMouseEnter={(event: SyntheticEvent) => {
                                        this.onMouseEnter({
                                            content: <NavigatorUserComponent />,
                                            x: (event.target as HTMLElement).getBoundingClientRect().right,
                                            margin: 'right'
                                        })
                                    }}
                                    onMouseLeave={this.onMouseLeave}
                                >
                                    <i className="fa fa-user"></i>
                                </Button>
                            </div>
                        </Navbar>
                    </div>
                    {
                        <NavigatorDropdownComponent
                            onMouseEnter={this.onMouseEnter}
                            onMouseLeave={this.onMouseLeave}
                            content={dropdown.content}
                            x={dropdown.x}
                            margin={dropdown.margin}
                        />
                    }
                </div>
            </header>
        )
    }
}

export default NavigatorComponent;