import React, { Component } from "react";
import Slider from "react-slick";
import { Image } from "react-bootstrap";

interface IProps { }
interface IState {
    nav1: any,
    nav2: any,
}

let slider1: any = null;
let slider2: any = null;

class ImageBoxComponent extends Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            nav1: null,
            nav2: null
        };
    }

    componentDidMount() {
        this.setState({
            nav1: slider1,
            nav2: slider2
        });
    }

    render() {
        let imgs = [
            "/static/images/product/_o_len_nam_e7swt801f_en_590k.jpg",
            "/static/images/product/_o_len_nam_e7swt801f_v_ng_590k.jpg",
            "/static/images/product/_o_len_nam_e7swt801f_xanh_r_u_590k.jpg",
        ]

        return (
            <div className="imagebox">
                <Slider
                    className="imagebox-view"
                    asNavFor={this.state.nav2}
                    ref={slider => (slider1 = slider)}
                    responsive={[
                        {
                            breakpoint: 576,
                            settings: {
                                dots: true
                            }
                        }
                    ]}
                >
                    {
                        imgs.map((o, i) => (
                            <div key={i} className="imagebox-view-item">
                                <Image src={o} />
                            </div>
                        ))
                    }
                </Slider>
                <Slider
                    className="imagebox-nav d-none d-md-block"
                    asNavFor={this.state.nav1}
                    ref={slider => (slider2 = slider)}
                    slidesToShow={imgs.length >= 5 ? 5 : imgs.length}
                    swipeToSlide={true}
                    focusOnSelect={true}
                    variableWidth
                >
                    {
                        imgs.map((o, i) => (
                            <div key={i} className="imagebox-tiny-item">
                                <div className="mask"></div>
                                <Image src={o} />
                            </div>
                        ))
                    }
                </Slider>
            </div>
        );
    }
}

export default ImageBoxComponent;