import React, { Component, SyntheticEvent } from "react";
import { Navbar, Nav, Button, Image, Form, Row, Col } from "react-bootstrap";
import { Link } from "react-router-dom";
import { IMegaDirectional, IDirectional } from "../../define/define.interface";

interface IProps {
    data: IMegaDirectional[] | IDirectional[]
}
interface IState { }

class NavigatorCategoryComponent extends Component<IProps, IState> {
    render() {
        const { data } = this.props;
        let categoryType = 0;
        if ((data as IMegaDirectional[]).every(o => typeof o.title !== 'undefined' && typeof o.list !== 'undefined')) categoryType = 1;
        else if ((data as IDirectional[]).every(o => typeof o.text !== 'undefined' && typeof o.url !== 'undefined')) categoryType = 2;

        return (
            <div className="d-flex">
                {
                    categoryType === 1 ? (
                        <>
                            {
                                (data as IMegaDirectional[]).map(({ title, list }, i) => (
                                    <div className="px-4 py-2" key={i}>
                                        <h5 className="p-3 text-bold text-13 text-uppercase">{title.text}</h5>
                                        <ul className="m-0 p-0">
                                            {
                                                list.map(({ text }, i) => (
                                                    <li className="border-bottom p-3" style={{ listStyle: 'none', width: '220px' }} key={i}>
                                                        <Link to="/category"><a className="link py-3">{text}</a></Link>
                                                    </li>
                                                ))
                                            }
                                        </ul>
                                    </div>
                                ))
                            }
                        </>
                    ) : categoryType === 2 ? (
                        <ul className="m-0 p-0">
                            {
                                (data as IDirectional[]).map(({ text, url }, i) => (
                                    < li className={`${i === data.length - 1 ? "" : "border-bottom"} p-3`} style={{ listStyle: 'none', width: '220px' }} key={i}>
                                        <Link to="/category">
                                            <a className="link py-3">{text}</a>
                                        </Link>
                                    </li>
                                ))
                            }
                        </ul>
                    ) : undefined
                }
            </div>
        )
    }
}

export default NavigatorCategoryComponent;